@mixin minorBtnShadow() {
    -webkit-box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
    -moz-box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
    -o-box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12); }

@mixin transitionProgress($time) {
    -webkit-transition: $time linear;
    -moz-transition: $time linear;
    -o-transition: $time linear;
    transition: $time linear; }

@mixin mainBtnShadow($color) {
    -webkit-box-shadow: 0px 4px 14px 2px $color;
    -moz-box-shadow: 0px 4px 14px 2px $color;
    -o-box-shadow: 0px 4px 14px 2px $color;
    box-shadow: 0px 4px 14px 2px $color; }

@mixin animationGradient($time) {
    -webkit-animation: Gradient $time ease infinite;
    -moz-animation: Gradient $time ease infinite;
    -o-animation: Gradient $time ease infinite;
    animation: Gradient $time ease infinite; }

@mixin keyframes($name) {
  @-webkit-keyframes #{$name} {
    @content; }
  @-moz-keyframes #{$name} {
    @content; }
  @-ms-keyframes #{$name} {
    @content; }
  @keyframes #{$name} {
    @content; } }

@include keyframes(Gradient) {
    0% {
        background-position: 0% 50%; }
    50% {
        background-position: 100% 50%; }
    100% {
        background-position: 0% 50%; } }

body {
    font-family: 'Encode Sans Condensed', sans-serif;
    background-color: #f1f1f1; }

p, h3 {
    margin: 0px; }

select {
    -webkit-appearance: none;
    -webkit-border-radius: 2px; }
.footer-form-block {
    overflow: hidden; }
.err {
    color: red;
    margin-bottom: 10px; }
.bad-valid {
    border: 1px solid red !important; }

.application {
    max-width: 1200px;
    margin: 0 auto; }
.dark-color {
    color: #000000; }

.right-create-task-form {
    display: inline-block;
    width: 39.5%;
    margin-bottom: 20px;
    margin-left: 15px;
    select {
        border-color: white;
        position: relative;
        top: -1px;
        padding-left: 10px;
        &:focus {
            outline: none; } } }

.left-create-task-form {
    float: left;
    width: 57%;
    margin-right: 8px;
    input#task-time {
        background: white;
        border-radius: 2px;
        padding-left: 10px;
        height: 43px;
        border: 1px solid #ffffff;
        box-shadow: none; } }

.addNewTask-form {
    width: 700px;
    display: inline-block;
    textarea {
        width: 100%;
        font-size: 16px;
        border-radius: 2px;
        height: 80px;
        border: 1px solid #ffffff;
        padding-left: 10px;
        resize: none;
        background: #ffffff;
        margin-top: 10px;
        margin-bottom: 5px;
        padding-top: 8px;
        &:focus {
            outline: none; } } }


.empty-buffer {
    height: 40px; }
.task-body {
    width: 748px;
    &:hover > .insert_comment {
        opacity: 1;
        @include transitionProgress(.30s); } }

.task-block {
    background: white;
    border-radius: 2px;
    width: 698px;
    padding: 12px 10px;
    overflow: hidden;
    display: inline-block;
    margin-bottom: 12px;
    padding-left: 5px;
    float: left;

    .rigth-task-info {
        float: left;
        width: 87%;
        overflow: hidden;
        &-body {
            float: left;
            width: 90%;
            .tasl-time {
                color: #afafaf;
                font-weight: 300; } }
        &-tail {
            padding-left: 10px;
            float: left;
            width: 10%; } }
    .task-status-block {
        float: left;
        width: 12%;
        padding-top: 9px; } }

.removeTask {
    font-size: 20px;
    color: red;
    font-family: sans-serif;
    cursor: pointer;
    position: relative;
    top: -9px;
    left: -2px;
    float: right; }

#progressBlock {
    background: #ecebeb;
    height: 5px;
    .progress-part {
        display: inline-block;
        height: 5px;
        background: #d6d6d6;
        width: 0%; }
    .successProgress {
        background-color: #25a59a;
        @include transitionProgress(.30s);
        position: relative;
        top: -11px; } }

.btn-main-style {
    border: 0px;
    font-size: 18px;
    margin-bottom: 20px;
    border-radius: 2px;
    padding: 12px 42px;
    color: white;
    background: linear-gradient(-90deg, #4a1b9e, #ce3ee6);
    background-size: 200% 200%;
    cursor: pointer;
    transition: all 400ms ease-in-out;
    @include animationGradient(3s);
    @include mainBtnShadow(#c36bd2);
    &:focus, &:hover {
        outline: none;
        transition: all 400ms ease-in-out;
        @include mainBtnShadow(rgba(195, 107, 210, 0)); } }

.infin {
    font-size: 35px;
    font-weight: 300;
    line-height: 0px;
    color: #902ec5;
    position: relative; }

.eternity-block {
    margin-bottom: 10px; }

.task-history-block {
    overflow: hidden;
    margin-top: 10px;
    margin-bottom: 10px;
    .history-day-block {
        float: left;
        border: 2px solid #008e82;
        border-radius: 2px;
        margin-right: 5px;
        cursor: pointer; } }

.progress-full {
    width: 100%;
    background-color: #25a59a;
    color: white; }

.progress-good {
    width: 80%;
    background-color: #25a59a; }

.progress-middle {
    width: 50%;
    background-color: #ffde3c; }

.progress-low {
    width: 25%;
    background-color: #ff4e16; }

.progress-empty {
    width: 3%;
    background-color: #ff1717; }

.history-progress {
    span {
        margin-left: 10px;
        margin-right: 10px; } }

.eternity-check {
    float: left; }

.weekday-settings-block {
    display: inline-block; }

.weekday-settings-manage {
    padding: {
        left: 50px;
        bottom: 10px;
        top: 3px; }
    span {
        border-radius: 2px;
        padding: 4px 35px;
        background: #00a89a;
        cursor: pointer;
        margin-right: 7px;
        color: white;
        @include minorBtnShadow(); } }

.weekday-settings-items {
    padding-left: 40px;
    .day-item {
        display: inline-block;
        margin: {
            left: 7px;
            right: 7px; }
        border: 2px solid #9612c8;
        width: 50px;
        height: 50px;
        line-height: 46px;
        border-radius: 25px;
        cursor: pointer;
        text-align: center;
        font-weight: 600;
        transition: all 400ms ease-in-out; }

    .opted {
        background: linear-gradient(-90deg, #4a1b9e, #ce3ee6);
        background-size: 200% 200%;
        cursor: pointer;
        transition: all 400ms ease-in-out;
        @include animationGradient(3s);
        @include mainBtnShadow(#c36bd2);
        color: #ffffff; } }

.none {
    display: none; }
.task-body {
    overflow: hidden; }
.antix-toast {
    background: #00a89a;
    box-shadow: antiquewhite;
    box-shadow: 0px 4px 14px 2px #1cad9eab; }

.insert_comment {
    opacity: 0;
    @include transitionProgress(.30s);
    background: url(../img/comment_add.svg);
    display: inline-block;
    width: 33px;
    height: 33px;
    background-repeat: no-repeat;
    float: left;
    cursor: pointer;
    margin: {
        left: 10px;
        top: -2px; } }

#antix-modal-for-comments {
    max-height: 60%;
    .task-comment {
        width: 100%;
        height: 8rem;
        background-color: transparent;
        resize: none;
        border-radius: 2px;
        border: 2px solid #7c10ba;
        font-size: 16px;
        padding: 7px 8px;
        &:focus {
            outline: none; } }
    .btn-main-style {
        margin-right: 20px; } }
